.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 180px;
  background:transparent;
  height: 56px;
}

.toolbar__logo {
  margin-left: 1rem;
  color: white;
  font-size: 35px;
}
.toolbar__navigation {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1rem;
}

.spacer {
  flex: 1;
}
.toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation-items li {
  padding: 0 .5rem;
}

.toolbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.toolbar_navigation-items a {
  color: white;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #fa923f;
}
